.header-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 80px;
}

.logo-title {
  text-align: center;
  width: 100%;
  text-transform: uppercase;
  font-size: 20px;
  position: relative;
  z-index: 10;
}

.menu-list {
  margin: 0;
  padding: 22px 0 22px 18px;
}

.menu-desktop {
  display: none;
  width: 20%;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: auto;
  padding: 0;
  margin-top: 12px;
}

li {
  list-style-type: none;
  letter-spacing: 2px;
  line-height: 30px;
}

li:hover {
  color: #4fbbb6;
}

.hamburger {
  width: 50px;
  height: 50px;
  margin: 0;
  cursor: pointer;
  margin-right: 60px;
  padding: 15px 0 10px 15px;
  display: block;
  position: relative;
  z-index: 1;
}

.bar {
  width: 25px;
  height: 4px;
  background-color: black;
  margin: 0 0 3px 5px;
}

.menu {
  background: white;
  width: 100%;
  position: absolute;
  top: -70px;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  background: rgba(255, 255, 255, 0.6);
}

.hamburger-checkbox {
  display: none;
}

.hamburger-checkbox:checked ~ .menu {
  top: 80px;
  opacity: 1;
}

.hamburger-checkbox:checked + .hamburger .bar1 {
  -webkit-transform: rotate(-45deg) translate(-6px, 5px);
      -ms-transform: rotate(-45deg) translate(-6px, 5px);
          transform: rotate(-45deg) translate(-6px, 5px);
}

.hamburger-checkbox:checked + .hamburger .bar2 {
  opacity: 0;
}

.hamburger-checkbox:checked + .hamburger .bar3 {
  -webkit-transform: rotate(45deg) translate(-5px, -5px) ;
      -ms-transform: rotate(45deg) translate(-5px, -5px) ;
          transform: rotate(45deg) translate(-5px, -5px) ;
}

/* desktop */
@media only screen and (min-width: 950px) {
  .header-container {
    margin: 0 90px 0 90px;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 200px;
  }

  .hamburger {
    display: none;
  }
  .menu {
    display: none;
  }
  .logo-container {
    -ms-flex-pack: center;
        justify-content: center;
  }
  .logo-title {
    font-size: 40px;
    padding-top: 25px;
  }
  .menu-desktop {
    display: -ms-flexbox;
    display: flex;
  }
}

/* tablet */
@media (min-width: 600px) and (max-width: 950px) {
  .header-container {
    margin: 0 90px 0 90px;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 200px;
  }
  .menu-desktop {
    width: 40%;
    display: -ms-flexbox;
    display: flex;
  }
  .hamburger {
    display: none;
  }
  .menu {
    display: none;
  }
  .logo-container {
    -ms-flex-pack: center;
        justify-content: center;
  }
  .logo-title {
    font-size: 40px;
    padding-top: 25px;
  }
}



