.body-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.hero-image-container {
  width: 100%;
}

.hero-image {
  width: 100%;
}

.hero-products {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-size: 18px;
  border-bottom: 1px solid grey;
  padding-top: 35px;
  margin-bottom: 35px;
}

@media (min-width: 900px) {
  .hero-products {
    font-size: 30px;
    padding-top: 50px;
    margin-bottom: 50px;
  }
  .hero-image-container {
    width: 85%;
  }
}



